'use client'

import { LoginRequest } from "@/src/lib/auth"
import { useForm } from "react-hook-form"
import { login } from "@/src/lib/auth"
import { toast } from "react-toastify"
import Cookies from "js-cookie"
import { useRouter } from "next/navigation"
import Image from 'next/image';
import css from './page.module.css';
import Link from 'next/link';


export default function LoginPage() {
    const { register: formRegister, handleSubmit, formState: { errors, isSubmitting }} = useForm<LoginRequest>()
    const router = useRouter()

    const onSubmit = async (data: LoginRequest) => {
        try {
            const res = await login(data)

            Cookies.set('token', res.token, {expires: 1})

            if (res.user.shopId) {
                Cookies.set('shopId', res.user.shopId, { expires: 1})
            }
            localStorage.setItem('user', JSON.stringify(res.user))
            toast.success(`Welcome back, ${res.user.name}`)
            router.push('/shop')
        } catch (err) {
            toast.error('Login failed')
            console.log(err)
        }
        }
        return (
    <div className='container'>
      <div className={css.titleCont}>
        <Image className={css.image} src="/pill.png" alt="pill" width={95} height={95}/>
        <h1 className={css.title}>
          Your medication, <br /> delivered Say goodbye to all{' '}
          <span className={css.span}>your healthcare</span> worries with us
        </h1>
      </div>
      <form className={css.form} action="submit">
        <div className={css.field}>
          <label className={css.label} htmlFor="name">
            User Name
          </label>
          <input className={css.input} key="name" type="text" />
        </div>
        <div className={css.field}>
          <label className={css.label} htmlFor="email">
            Email address
          </label>
          <input className={css.input} key="email" type="text" />
        </div>
        <div className={css.field}>
          <label className={css.label} htmlFor="phone">
            Phone number
          </label>
          <input className={css.input} key="phone" type="text" />
        </div>
        <div className={css.field}>
          <label className={css.label} htmlFor="password">
            Password
          </label>
          <input className={css.input} key="password" type="text" />
        </div>

        <button className={css.button} type="submit" onClick={handleSubmit(onSubmit)}>Register</button>
      </form>
      <Link className={css.link} href={'/login'}>
       Already have an account?
      </Link>
    </div>
  );
    }

